.address-container{background-color:#f2fafa;border-radius:4px;color:#506175;padding:18px 24px;width:248px;word-break:break-word}.address-container .currency-container{-webkit-box-align:baseline;-ms-flex-align:baseline;align-items:baseline;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.address-container .currency-container .currency-amt{color:#003945;font-size:20px;font-weight:500}.address-container .currency-container .currency-type{color:#536d8b;font-size:20px;font-weight:500}.address-container .currency-container img{width:36px}.address-container .currency-container p{margin:10px 0}.address-container p{font-size:16px;font-weight:600}.address-container .address{width:100%;word-wrap:break-word;cursor:pointer}.icon-matcher{-webkit-box-align:center;-ms-flex-align:center;align-items:center;background-color:#497391;border-radius:50%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:36px;width:36px}.icon-matcher img{height:26px !important;width:auto !important}