@media all and (min-width: 1025px){.decision-tree-modal .modal-dialog{width:400px;position:fixed;right:50px}}
.decision-tree-modal .modal-content{border-radius:10px}
.decision-tree-modal .modal-body{background-color:transparent}
.decision-tree-modal .decision-tree-search .multiselect{height:100%}
.decision-tree-modal .decision-tree-search .multiselect__tags{height:100%}
.decision-tree-modal .decision-tree-search .multiselect__input{padding-left:45px !important;padding-right:50px !important;height:100%;border:0;background-color:transparent;width:100% !important}
.decision-tree-modal .decision-tree-search .multiselect__content-wrapper{overflow:auto;max-height:550px !important;background-color:#f1f1f1;margin-top:3px;-webkit-box-shadow:0px 4px 10px #00000033;box-shadow:0px 4px 10px #00000033}
@media all and (max-width: 1024px){.decision-tree-modal .decision-tree-search .multiselect__content-wrapper{max-height:450px !important}}
.decision-tree-modal .decision-tree-search .multiselect__element{border-bottom:1px solid #e0e0e0;cursor:pointer}
.decision-tree-modal .decision-tree-search .multiselect__element span{display:block}
.decision-tree-modal .decision-tree-search .multiselect__element span span{padding:10px 20px;font-size:13px;font-weight:400}
.decision-tree-modal .decision-tree-search .multiselect__element:hover{background-color:#ececec}
.decision-tree-modal .decision-tree-search .multiselect__element:hover span{font-weight:400}
.decision-tree-modal .decision-tree-search .multiselect__placeholder{position:absolute;top:14px;left:45px}
.decision-tree-modal .decision-tree-search .multiselect__content{width:100%}
.decision-tree-modal .decision-tree-search .no-result{padding:10px 20px;font-size:14px;font-weight:600;text-align:center;width:100%;background-color:#000;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;color:#fff}
.decision-tree-modal .decision-tree-search .no-result i{font-size:22px;margin-right:10px}